import React, { Component } from 'react';
import './Game.scss';

import Timer from '../Timer/Timer.js'


class Game extends Component {
  intervalID =  0;
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  state = {
    gameActive: false,
    gameOver: false,
    playerWidth: 30,
    playerHeight: 30,
    playerX: 0,
    playerY: 0,
    playerSpeedX: 0,
    playerSpeedY: 0,
    obstacles: [],
    time: 0
  }

  componentDidMount() {
    this.drawStart();
    this.drawTutorial();
  }

  updateObstacles() {
    const canvas = document.getElementById("game-canvas")
    
    let obstacleArr = this.state.obstacles;
    if((this.state.time % 10000) === 2500) {
      obstacleArr.push(this.addObstacle());
      this.setState({
        obstacles: obstacleArr
      });
    } else {
      // No need to add obstacles.
      obstacleArr.forEach(o => {
      //  this.state.playerX >= canvas.width - 20 || this.state.playerX <= 0
      //|| this.state.playerY >= canvas.height - 20 || this.state.playerY <= 0
        if(o.x >= canvas.width - o.obstacleW) {
          o.obstacleSpeedX = -3;
        }
        if(o.x <= 5) {
          o.obstacleSpeedX = 3;
        }
        if(o.y >= canvas.height) {
          o.obstacleSpeedY = -3;
        }
        if(o.y <= 0) {
          o.obstacleSpeedY = 3;
        }
        o.x += o.obstacleSpeedX;
        o.y += o.obstacleSpeedY;
      });
    }
  }

  addObstacle() {
    const canvas = document.getElementById("game-canvas")

    let dimensions = 30;
    let obstacle = {
      x: ((Math.random() * canvas.width / 2) + canvas.width / 2) - this.state.playerX,
      y: ((Math.random() * canvas.height / 2) + canvas.height / 2) - this.state.playerX,
      obstacleW: dimensions,
      obstacleH: dimensions,
      obstacleSpeedX: (Math.random() * 3) - 1,
      obstacleSpeedY: (Math.random() * 3) - 1
    }

    return obstacle;
  }

  draw() {
    const canvas = document.getElementById("game-canvas")
    const ctx = this.refs.canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height)

    this.setState({
      playerX: this.state.playerX + this.state.playerSpeedX,
      playerY: this.state.playerY + this.state.playerSpeedY,
    })

    this.updateObstacles();

    if(this.collision()) {
      // Kill player, end game
    } else {
      // Player is still alive
    }

    let x = this.state.playerX;
    let y = this.state.playerY;
    ctx.fillRect(x, y, this.state.playerWidth, this.state.playerHeight);

    this.state.obstacles.forEach(o => {
      ctx.fillRect(o.x, o.y, o.obstacleW, o.obstacleH);
    })
  }

  drawStart() {
    const canvas = document.getElementById("game-canvas")
    const ctx = this.refs.canvas.getContext("2d");
    ctx.fillStyle = "white";
    ctx.fillRect(canvas.width / 2 - this.state.playerWidth / 2, canvas.height/2 - this.state.playerHeight / 2,
      this.state.playerWidth, this.state.playerHeight);
    this.setState({
      playerX: canvas.width / 2 - this.state.playerWidth / 2,
      playerY: canvas.height / 2 - this.state.playerHeight / 2,
      playerSpeedY: 0,
      playerSpeedX: 0,
      gameActive: false,
      gameOver: false,
    })
  }

  drawTutorial() {
    const canvas = document.getElementById("game-canvas")
    const ctx = this.refs.canvas.getContext("2d");
    ctx.font = "32px Libre Baskerville";
    ctx.fillStyle = "white";
    ctx.textAlign = "center";
    ctx.fillText("WASD is MOVE", canvas.width/2, canvas.height/2 - 50);
  }

  startGame() {
    this.intervalID = setInterval(() => {
      this.draw();
      this.setState({
        gameActive: true,
        gameOver: false,
        time: this.state.time + 10
      })
    }, 10);
  }

  endGame() {
    clearInterval(this.intervalID)
    // Draw try again screen.
    let score = document.getElementById("timer").innerHTML;
    alert(`Game Over\nTime Survived: ${score}`)
    this.setState({
      obstacles: []
    });
    this.drawStart();
    this.startGame();
  }

  collisionWithObstacle() {
    let o = this.state.obstacles;
    let flag = false;

    for (let i = 0; i < o.length; i++) {
      let px = this.state.playerX;
      let py = this.state.playerY;
      let ox = Math.floor(o[i].x);
      let oy = Math.floor(o[i].y);
      let od = Math.floor(o[i].obstacleW);
      if((ox <= px && px <= ox + od) && (oy <= py && py <= oy + od)) {
        flag = true;
      } else {
        flag = false;
      }
    }
    
    return flag;
  }

  collision() {
    const canvas = document.getElementById("game-canvas")

    if(this.state.playerX >= canvas.width - 20 || this.state.playerX <= 0
      || this.state.playerY >= canvas.height - 20 || this.state.playerY <= 0) {
      this.endGame();
    } else if (this.collisionWithObstacle()) {
      this.endGame();
      // Collision with obstacle
    } else {
      // No collision
    }
    return false;
  }

  updatePlayer(e) {
    e.preventDefault();

    switch(e.key) {
      case "w":
      case "ArrowUp":
        this.setState({ 
          playerSpeedY: -2,
          playerSpeedX: 0
        })
        break;
      case "a":
      case "ArrowLeft":
        this.setState({ 
          playerSpeedY: 0,
          playerSpeedX: -2
        })
        break;
      case "s":
      case "ArrowDown":
        this.setState({ 
          playerSpeedY: 2,
          playerSpeedX: 0
        })
        break;
      case "d":
      case "ArrowRight":
        this.setState({ 
          playerSpeedY: 0,
          playerSpeedX: 2
        })
        break;
      case "Enter":
        // Restart game
        break;
      default:
        break;
    }
    
    if (e.touches === undefined) {
      // do nothing
    }
    else {
      let x = e.touches[0].clientX;
      let y = e.touches[0].clientY;

      if((x < 150) && (y < 550) && (y > 450)) {
        this.setState({ 
          playerSpeedY: 0,
          playerSpeedX: -2
        })
      } else if((x > 150) && (y < 550) && (y > 450)) {
        this.setState({ 
          playerSpeedY: 0,
          playerSpeedX: 2
        })
      } else if(y < 400) {
        this.setState({ 
          playerSpeedY: -2,
          playerSpeedX: 0
        })
      } else if(y > 400) {
        this.setState({ 
          playerSpeedY: 2,
          playerSpeedX: 0
        })
      }
    }
  }

  handleKeyPress(e) {
    if(!this.state.gameActive && !this.state.gameOver) {
      // Do nothing
    } else {
      // Game is in progress, update player
      this.updatePlayer(e);
    }
  }

  handleClick(e) {
    if(!this.state.gameActive) {
      this.startGame();
      this.setState({
        gameActive: true
      })
    } else if (this.state.gameActive && this.state.gameOver) {
      // Handle new game logic calls
    } else {
      // console.log("Game is still in progress")
      // Do nothing
    }
  }

  render () {
    return (
      <React.Fragment>
        <div className="wrapper">
          {this.state.gameActive ? <Timer game={true}/> : <div className="timer">&nbsp;</div>}
        </div>
        <div>
          <canvas width={this.props.width} height={this.props.height} ref="canvas" id="game-canvas" className="game-canvas" tabIndex="0"
          onClick={(e) =>{this.handleClick(e.nativeEvent)}}
          onKeyDown={(e) =>{this.handleKeyPress(e.nativeEvent)}}
          onTouchStart={(e) =>{this.handleKeyPress(e.nativeEvent)}}
          ></canvas>
        </div>
      </React.Fragment>
    );
  }
};

export default Game;