import React, { useState, useEffect } from 'react';

function Timer( {game} ) {
  const [time, setTime] = useState(0);
  const [timeStr, setTimeStr] = useState("00:00:00");

  // Pads n to a length of 2
  function padNum(n) {
    let nString = n.toFixed(0) + "";
    return nString.length < 2 ? "0" + nString : nString;
  }

  useEffect(() => {
    let interval = null;
    if (game) {
      interval = setInterval(() => {
        let seconds = padNum(time % 60);
        let minutes = padNum(parseInt(time / 60));
        let hours = padNum(parseInt(minutes / 60));;

        let totalTime = `${hours}:${minutes}:${seconds}`; 

        setTime(time => time + 1);
        setTimeStr(timeStr => totalTime);
      }, 1000);
    } else if (!game && time !== 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [time]);

  return (
    <div className="wrapper">
      {game ? <div className="timer" id="timer">{timeStr}</div> : ""}
    </div>
  );
};

export default Timer;